import { Input } from "@material-tailwind/react";
import { InputHelperText } from "../InputHelperText";

export function InputWithHelperText(props) {
  const { register, errorMsg, extraErrorMsg, label, type = "text" } = props;
  return (
    <div>
      {/* <div className="w-[32rem]"> */}
      <Input
        {...register}
        error={Boolean(errorMsg)}
        type={type}
        label={label}
      />
      {errorMsg && <InputHelperText errorMsg={extraErrorMsg || errorMsg} />}
    </div>
  );
}
