import { useForm, Form } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import InputMask from "react-input-mask";
import * as yup from "yup";
import {
  Card,
  CardBody,
  Input,
  Button,
  Typography,
} from "@material-tailwind/react";
import "./App.css";
import { InputHelperText } from "./components/InputHelperText";
import { InputWithHelperText } from "./components/InputWithHelperText";

const schema = yup
  .object({
    firstName: yup.string().required("Обязательное поле").min(2),
    lastName: yup.string().required("Обязательное поле").min(2),
    middleName: yup.string(),
    email: yup
      .string()
      .required("Обязательное поле")
      .email("Пожалуйста, введите действительный e-mail адрес"),
    age: yup
      .number("Пожалуйста, введите действительный возраст")
      .positive("Пожалуйста, введите действительный возраст")
      .integer("Пожалуйста, введите действительный возраст")
      .required("Обязательное поле"),
    childAge: yup
      .number("Пожалуйста, введите действительный возраст")
      .positive("Пожалуйста, введите действительный возраст")
      .integer("Пожалуйста, введите действительный возраст")
      .required("Обязательное поле"),
    phone: yup
      .string()
      .required("Обязательное поле")
      .length(18, "Пожалуйста, введите действительный номер телефона"),
  })
  .required();

function App() {
  return (
    <div className="App">
      <SimpleRegistrationForm />
    </div>
  );
}

export default App;

export function SimpleRegistrationForm() {
  const {
    register,
    formState: { errors },
    control,
  } = useForm({
    resolver: yupResolver(schema),
  });

  // console.log(register("lastName", { required: true }));
  // 1-Фамилия
  // 2-Имя
  // 3-Отчество
  // 4-Ваш возраст
  // 5-Номер телефона
  // 6-E-mail
  // 7-Возраст вашего ребёнка /детей
  return (
    <Card color="transparent" shadow={false} className="App-card">
      <CardBody>
        <Typography variant="h5" color="blue-gray">
          Пожалуйста, заполните данные
        </Typography>
        {/* <Typography color="gray" className="mt-1 font-normal">
        Enter your details to register.
      </Typography> */}
        <Form
          encType="application/json"
          action="http://158.160.82.110:3000/save-data"
          onSuccess={() => {
            // alert("Your application is updated.");
          }}
          onError={() => {
            alert("Что-то пошло не так...");
          }}
          control={control}
          className="mt-8 pl-5 pr-5 mb-8"
          style={{ width: "100%" }}
        >
          <div className="mb-4 flex flex-col gap-6">
            <InputWithHelperText
              label={"Фамилия"}
              register={register("lastName")}
              errorMsg={errors.lastName?.message}
            />
            <InputWithHelperText
              label={"Имя"}
              register={register("firstName")}
              errorMsg={errors.firstName?.message}
            />
            <InputWithHelperText
              label={"Отчество (при наличии)"}
              register={register("middleName")}
              errorMsg={errors.middleName?.message}
            />
            <InputWithHelperText
              label={"Email"}
              register={register("email")}
              errorMsg={errors.email?.message}
            />

            <div>
              <InputMask
                mask="+7 (999) 999-99-99"
                maskChar={null}
                {...register("phone")}
              >
                {(inputProps) => (
                  <Input
                    {...inputProps}
                    error={errors.phone?.message}
                    type="tel"
                    size="lg"
                    label="Номер телефона"
                  />
                )}
              </InputMask>
              {errors.phone?.message && (
                <InputHelperText errorMsg={errors.phone?.message} />
              )}
            </div>
            <InputWithHelperText
              label={"Ваш возраст"}
              register={register("age")}
              errorMsg={errors.age?.message}
              extraErrorMsg={"Пожалуйста, введите действительный возраст"}
            />
            <InputWithHelperText
              label={"Возраст ребëнка"}
              register={register("childAge")}
              errorMsg={errors.childAge?.message}
              extraErrorMsg={"Пожалуйста, введите действительный возраст"}
            />
          </div>
          <Button
            className="mt-6 hover:scale-[1.02] focus:scale-[1.02] active:scale-100"
            fullWidth
            type="submit"
          >
            Отправить
          </Button>
        </Form>
      </CardBody>
    </Card>
  );
}
